import Card from 'react-bootstrap/Card';
import { Slide } from "react-awesome-reveal";
import { CustomCard } from "./styles"

interface CardBlockProps {
  bg: string;
  name: string;
  body: string;
  image: string;
}

const OurTeamBlock = () => {
  const scrollTo = (id: string) => {
    const element = document.getElementById(id) as HTMLDivElement;
    element.scrollIntoView({
      behavior: "smooth",
    });
  };

  const CardBlock = ({ bg, name, body, image }: CardBlockProps) => {
    return (
      <Card className={`bg${bg}`}>
        <Card.Title>{name}</Card.Title>
        <Card.Img variant="top" src={image} />
        <Card.Body>
          <Card.Text>{body}</Card.Text>
        </Card.Body>
      </Card>
    );
  };

  return (

    <Slide direction="left">
      <h6>Team</h6>
      <CustomCard>
        <CardBlock bg='1' name="Mary" body="Artist. Creative director." image="img\icons\Logo_PNG.png" />
        <CardBlock bg='2' name="Gotlib" body="Artist/Animator. Game designer." image="img\icons\Logo_PNG.png" />
        <CardBlock bg='3' name="Line" body="Coder/Techie. NFT enthusiast." image="img\icons\Logo_PNG.png" />
        <CardBlock bg='4' name="Lucky" body="Coder/Trainee. Fox boy." image="img\icons\Logo_PNG.png" />
      </CustomCard>
    </Slide>

  );
};

export default (OurTeamBlock);

