import styled from "styled-components";





export const CustomCard = styled("div")`
  .card {
    
    width: 18rem;
    text-align: center;
    position: relative;
    display: inline-block;
  }
  
  .card-title {
    color: #fff;
    font-size: 20px;
  }

  .card-text {
    color: #000;
    font-size: 16px;
  }

  .bg1 {
    background: url("/img/png/teamPaper1.png");
    background-size: 100% 100%;
  }

  .bg2 {
    background: url("/img/png/teamPaper2.png");
    background-size: 100% 100%;
  }
  .bg3 {
    background: url("/img/png/teamPaper3.png");
    background-size: 100% 100%;
  }
  .bg4 {
    background: url("/img/png/teamPaper4.png");
    background-size: 100% 100%;
  }


`;